import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import {connect} from 'react-redux'

import QuestionList from './components/QuestionList'
import {initializeQuestions} from './reducers/questionReducer'

import ResultList from './components/ResultList'
import Home from './components/Home'
import {Table} from 'react-bootstrap'

class App extends React.Component {

  componentWillMount = async () => {
    await this.props.initializeQuestions()
  }

  render() {
    return (
      <div className="App">
        <div className='container'>
          <Router>
            <div>
              <Table>
                <tbody>
                  <tr>
                    <td><Link to="/"><h5>home</h5></Link></td>
                    <td><Link to="/start"><h5>start</h5></Link></td>
                  </tr>
                </tbody>
              </Table>
            <Route exact path="/" render={() => <Home />} />
            <Route exact path="/start" render={() => <QuestionList />} />
            <Route exact path="/results" render={() => <ResultList />} />
            <br/>
          </div>
        </Router>
      </div>
    </div>
  )
  }
}

const mapStateToProps = (state) => {
    return {
      questions: state.questions,
      answers: state.answers,
      results: state.results
    }
}

export default connect(
  mapStateToProps,
  {initializeQuestions}
)(App)



