import React from 'react'
import {Table} from 'react-bootstrap'
import {connect} from 'react-redux'
import { getResults } from '../reducers/resultReducer'


class ResultList extends React.Component {
  componentWillMount = async () => {
    await this.props.getResults(this.props.answers)
  }

  render(){
      if(this.props.results === undefined | this.props.results.length === 0) {
        return <div></div>
      }
      console.log(this.props.results)
      const results = this.props.results
      return (
        <div>
          <h2>Results</h2>
          <h5>Here are the recommended models listed from most suitable to least matching.</h5>
          <Table>
            <tbody>
            {results.map(a =>
              <tr key = {a.model}>
                <td>
                  <a href={a.link}>
                    <img src={a.photo} alt={a.model} style={{width: 250, marginRight:5}} />
                    <br/>
                    {a.model} &nbsp;({Math.round((100000-a.score)/1000)} % match)
                  </a>
                </td>
              </tr>
            )}
            </tbody>
          </Table>
        </div>
      )
  }
}

const mapStateToProps = (state) => {
    return {
      questions: state.questions,
      answers: state.answers,
      results: state.results
    }
}


export default connect(
  mapStateToProps,{getResults}
)(ResultList)



