import React from 'react'
import Question from './Question'
import { getResults } from '../reducers/resultReducer'
import { addAnswer, removeAnswer } from '../reducers/answerReducer'
import { BrowserRouter as Router, Route, Link } from 'react-router-dom'
import {connect} from 'react-redux'

class QuestionList extends React.Component {

  render() {
    const question = this.props.visibleQuestion

    if(question === undefined) {
      return <div></div>
    }

    const backButton = {display: this.props.answers.length >0 ? '' : 'none', color: '#0080ff', fontSize: 'large', paddingRight: 70}
    const resultButton = {display: this.props.answers.length >0 ? '' : 'none', color: '#0080ff', fontSize: 'large', paddingLeft: 80 }
    const skipButton = {color: '#0080ff', fontSize: 'large', paddingLeft: 80 }



    if(question === null) {
      const home_style = {
        backgroundColor: '#222',
        padding: 20,
        marginBottom: 15,
        color: 'white'
      }


      return (
        <div>
          <div style={home_style}>
            <p style={{paddingTop: 20, paddingBottom: 10, fontSize:'large'}}>Almost done. Click results below to see which models match your preferations the most.</p>
          </div>
          <h4 style={backButton} onClick={this.previousQuest}>Back</h4>
          <Link to="/results"><h5>Results</h5></Link>
        </div>
      )
    }
    return(
      <div>
        <Question key={question.attribute} question= {question} answerHandler={this.handleAnswer}/>
        <a style={backButton} onClick={this.previousQuest}>Back</a>
        <a style={skipButton} onClick={this.skipQuestion}>Skip</a>
        <br/>
        <br/>
      </div>
    )
  }
/*
<div>
{qs.map(q =>
  <Question key={q.attribute} visible={isVisible(q_nr, qs, q.attribute)} question= {q} answerHandler={this.handleAnswer}/>
)}
<a style={backButton} onClick={this.previousQuest}>Back</a>
<a style={skipButton} onClick={this.skipQuestion}>Skip</a>

<br/>
<br/>
</div>
showResults = async() => {
  const results = await resultService.showResults(this.state.answers)
  this.setState({results})
}
*/
  skipQuestion = () => {
    this.props.addAnswer(null)
  }

  previousQuest = () => {
    this.props.removeAnswer()
  }

  handleAnswer = (attr, answer) => {
    let newAns = {
      "attribute" : attr,
      "answer" : answer
    }
    this.props.addAnswer(newAns)
  }

}

/*
import {connect} from 'react-redux'

  handleAnswer = (answer) => async (e) => {
    e.preventDefault()
    this.props.addAnswer(answer)
  }

}

*/
const questionToShow = (questions, n, attr) => {
  if(questions.length === n){
    return null
  } else {
    const questionToShow = questions.filter(q => q.attribute === questions[n].attribute)
    return questionToShow[0]
  }
}

const mapStateToProps = (state) => {
    return {
      visibleQuestion: questionToShow(state.questions, state.answers.length, state.questions[state.answers.length]),
      answers: state.answers
    }
}

export default connect(
  mapStateToProps,
  {addAnswer, removeAnswer
  }
)(QuestionList)



