import React from 'react'
//import {Table} from 'react-bootstrap'

// add answerHandler
const Answer = ({ answer, answerHandler }) => {
  if(answer === undefined) {
    return <div></div>
  }
  return (
    <div>
      <a>{answer.content}</a>
    </div>
  )
}

export default Answer



