import React from 'react'
import '../App.css';

const Home = () => {
  const home_style = {
    backgroundColor: '#222',
    padding: 20,
    color: 'white',
    borderRadius: 4
  }

    return(
      <div style={home_style}>
        <h2 style={{paddingBottom:10}}>Welcome to Bike Assistant</h2>
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Pelago-Brooklyn.jpg" alt="Brooklyn" style={{width: 100, marginRight:5, borderRadius: 2}}/>
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Hanko-Street.jpg" alt="Hanko Street" style={{width: 100, marginRight:5, borderRadius: 2}}/>
        <img src="https://www.pelagobicycles.com/media//wysiwyg/bikes2018/Stavanger_Commuter_1.jpg" alt="Stavanger Commuter" style={{width: 100, borderRadius: 2}}/>
        <p style={{paddingTop: 25, fontSize: 'large'}}>The idea is to give you a recommendation so that you have more than a hunch of what you are after.</p>
        <p style={{paddingTop: 15, paddingBottom: 25}}>[ If you find it challenging answering to a question, feel free to skip it. ]</p>
      </div>
    )
}

export default Home



